@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display|Source+Sans+Pro:200,400);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.colour3{background:#001fcc}.colour2{background:#0012ff}.colour1{background:#3633ff}.colour4{background:#002499}.colour5{background:#002166}body{font-family:"Open Sans Condensed", sans-serif;font-weight:400;font-size:20px;color:black;background-color:white;box-sizing:content-box;margin:0 auto}

.homepage h1,.homepage h2,.homepage h3,.homepage h4,.homepage h5,.homepage h6{font-family:"Playfair Display", serif}.homepage p,.homepage a{font-family:"Source Sans Pro", sans-serif}.homepage a{background-color:goldenrod;text-decoration:none;color:white;border-radius:0.25rem;text-align:center;display:inline-block;transition:all 0.3s}.homepage a:hover{opacity:0.6}.homepage .hero{background:url("https://placekitten.com/1800/410") center;background-size:cover;padding:4rem 2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));align-items:center}.homepage .hero>*{color:white;text-shadow:2px 2px black}.homepage .hero>h1{font-size:4rem;padding-bottom:1rem}.homepage .hero>article>p{font-size:1.5rem;font-weight:200}.homepage .hero>article>a{padding:1rem;margin-top:0.75rem}.homepage .breweries{padding:2rem}.homepage .breweries>ul{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));grid-gap:1rem;list-style-type:none}.homepage .breweries>ul>li{border:1px solid #e2e2e2;border-radius:0.5rem}.homepage .breweries>ul>li>figure{max-height:220px;overflow:hidden;border-top-left-radius:0.5rem;border-top-right-radius:0.5rem;position:relative}.homepage .breweries>ul>li>figure>img{width:100%}.homepage .breweries>ul>li>figure>figcaption{position:absolute;bottom:0;background-color:rgba(0,0,0,0.7);width:100%}.homepage .breweries>ul>li>figure>figcaption>h3{color:white;padding:0.75rem;font-size:1.25rem}.homepage .breweries>ul>li>p{font-size:1rem;line-height:1.5;padding:1rem 0.75rem;color:#666666}.homepage .breweries>ul>li>a{padding:0.5rem 1rem;margin:0.5rem}

.colour3{background:#001fcc}.colour2{background:#0012ff}.colour1{background:#3633ff}.colour4{background:#002499}.colour5{background:#002166}.header{background-color:white;border-bottom:1px solid black;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:1fr;grid-column-gap:0px;grid-row-gap:0px;padding:15px 50px 15px 50px;box-shadow:3px 3px 5px 6px #ccc}.header a{text-decoration:none;color:#001fcc}.header .title{color:#0012ff;font-weight:200;font-size:40px;text-transform:uppercase}.header .title:hover{color:#0012ff;font-size:40px}.header .subtitle{color:#002499;font-weight:300;font-style:italic;font-size:25px;padding-left:15px;vertical-align:text-bottom}.header .options{color:#001fcc;font-size:22px;font-weight:300;text-transform:uppercase;display:flex;align-items:center;justify-content:flex-end}.header .options .option{margin:5px 10px 5px 10px}.header .options .option:hover{color:#0012ff}

.sign-in-sign-up-container{margin:30px 0}.sign-in-sign-up{display:flex;max-width:850px;justify-content:space-between;margin:0 auto}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:380px;display:flex;flex-direction:column}.sign-in h2{margin:0}.sign-in h3{margin:10px 0}.sign-in .sign-in-buttons button{width:100%;margin-top:10px;margin-bottom:10px}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:18px;background-color:black;color:white;text-transform:uppercase;font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4 !important;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button .margin-right{margin-right:5px}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}.sign-up button{width:100%;margin-top:10px;margin-bottom:10px}

