.custom-button {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 18px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-weight: bolder;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }

  &.google-sign-in {
    // official google blue color
    background-color: #4285f4 !important;
    color: white;

    &:hover {
      background-color: #357ae8;
      border: none;
    }
  }

  .margin-right {
    margin-right: 5px;
  }
}
